export default {
    layout: 'Adm-content',
    computed: {
        years() {
            const year = new Date().getFullYear();
            let data = Array.from({ length: (year + 1) - 2020 }, (value, index) => 2020 + index);
            return data.slice().reverse();
        },

    },
    mounted() {
        this.getDepartment();
    },
    data() {
        return {
            batch: ({
                department: '',
                duration:'',
                group: '',
                shift: '',
                batch_name: '',
                said_fee: '',
                common_scholarship: '',
                no_of_seat: '',
                year: '',
                session: '',
                admission_season: '',               
                id_card_expiration_date: '',
                class_start_date: '',
                last_data_of_admission: '',
                payment_system: '',
                admission_start_date: '',
            }),

            errors: '',
            loading: false,
            departments: [],
            shifts: [],
            groups: [],
            campuses: [],
            paymentSystems: [],

        }
    },
    methods: {

        sessionYear(year) {
            return `${year}-${year + 1}`;
        },

        getDepartment() {
            this.$axios
                .$get("/admission/department")
                .then((response) => {
                    this.departments = response;
                })
                .catch((error) => {
                    console.log(error);
                    this.errors = error.response.message.errors;
                });
        },




        createBatch() {
            // console.log(this.batch);
            this.$axios
                .$post("/admission/batch-add", this.batch)
                .then((response) => {
                    this.$toaster.success(response.message);
                    this.batch = "";

                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                });

        }

    },

}
